'use server';

/**
 * @fileOverview AI Symptom Checker flow.
 *
 * This file defines a Genkit flow for analyzing user-provided symptoms and providing potential cures and severity assessments.
 *
 * @fileOverview
 * - `aiSymptomChecker`: Asynchronously analyzes user-provided symptoms, suggests potential cures, and assesses severity.
 * - `AISymptomCheckerInput`: Defines the input schema for the aiSymptomChecker function.
 * - `AISymptomCheckerOutput`: Defines the output schema for the aiSymptomChecker function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const AISymptomCheckerInputSchema = z.object({
  symptoms: z
    .string()
    .describe("The user's symptoms, described in detail."),
});

export type AISymptomCheckerInput = z.infer<typeof AISymptomCheckerInputSchema>;

const AISymptomCheckerOutputSchema = z.object({
  diagnosis: z.string().describe('The potential diagnosis based on the symptoms.'),
  cure: z.string().describe('Suggested cures or treatments for the diagnosis.'),
  severity: z
    .enum(['low', 'moderate', 'high'])
    .describe(
      'The severity assessment of the condition, indicating whether doctor consultation or hospitalization is recommended.'
    ),
  recommendedDoctors: z.array(z.string()).describe('List of doctor specializations recommended based on the diagnosis.')
});

export type AISymptomCheckerOutput = z.infer<typeof AISymptomCheckerOutputSchema>;

export async function aiSymptomChecker(input: AISymptomCheckerInput): Promise<AISymptomCheckerOutput> {
  return aiSymptomCheckerFlow(input);
}

const prompt = ai.definePrompt({
  name: 'aiSymptomCheckerPrompt',
  input: {schema: AISymptomCheckerInputSchema},
  output: {schema: AISymptomCheckerOutputSchema},
  prompt: `You are an AI assistant specialized in providing preliminary medical assessments based on user-reported symptoms.

  Analyze the following symptoms and provide a potential diagnosis, suggest possible cures or treatments, assess the severity of the condition (low, moderate, or high), and recommend relevant doctor specializations if needed.

  Symptoms: {{{symptoms}}}

  Respond in a structured format with diagnosis, cure, severity, and recommendedDoctors as described in the output schema.
  `,
});

const aiSymptomCheckerFlow = ai.defineFlow(
  {
    name: 'aiSymptomCheckerFlow',
    inputSchema: AISymptomCheckerInputSchema,
    outputSchema: AISymptomCheckerOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
