// cure-explanation.ts
'use server';

/**
 * @fileOverview Explains potential cures suggested by the AI symptom checker.
 *
 * - explainCure - A function that explains the potential cures.
 * - ExplainCureInput - The input type for the explainCure function.
 * - ExplainCureOutput - The return type for the explainCure function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const ExplainCureInputSchema = z.object({
  cure: z.string().describe('The name of the cure to explain.'),
  symptoms: z.string().describe('The symptoms the user is experiencing.'),
});
export type ExplainCureInput = z.infer<typeof ExplainCureInputSchema>;

const ExplainCureOutputSchema = z.object({
  explanation: z.string().describe('A detailed explanation of the cure and how it addresses the symptoms.'),
});
export type ExplainCureOutput = z.infer<typeof ExplainCureOutputSchema>;

export async function explainCure(input: ExplainCureInput): Promise<ExplainCureOutput> {
  return explainCureFlow(input);
}

const prompt = ai.definePrompt({
  name: 'explainCurePrompt',
  input: {schema: ExplainCureInputSchema},
  output: {schema: ExplainCureOutputSchema},
  prompt: `You are a helpful AI assistant providing detailed explanations of potential cures for medical symptoms.

  User Symptoms: {{{symptoms}}}
  Cure: {{{cure}}}

  Explain the cure in detail, including how it addresses the symptoms and any important considerations or side effects.`,
});

const explainCureFlow = ai.defineFlow(
  {
    name: 'explainCureFlow',
    inputSchema: ExplainCureInputSchema,
    outputSchema: ExplainCureOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
